import WizardConfig, { PrefixEntry } from '@pldn/ldwizard/types/WizardConfig'
import logo from './src/assets/logo.png'
import homepageMarkdown from './src/assets/landing.md'
import exampleCSV from './src/assets/kerken-nederland-geodatawizard.csv'
import {
  van_adres_en_woonplaats_naar_kkg_openbareRuimte,
  van_bag_id_naar_kkg_iri,
  van_postcode_en_huisnummer_naar_kkg_nummeraanduiding,
  van_wkt_punt_naar_kkg_gebouw,
  van_wkt_punt_naar_kkg_perceel,
  van_woonplaats_naar_kkg_woonplaats,
  van_bag_pand_id_naar_kkg_geometrie,
  van_bag_pand_id_naar_kkg_geometrie_bulk,
  van_bag_vbo_id_naar_adres_bulk

} from './src/transformations/index.ts'

const elasticsearchEndpoint = 'https://api.labs.kadaster.nl/datasets/GeoData-Wizard/vocabulaires/services/search/elasticsearch/'
const wizardConfig: WizardConfig = {
  appName: 'GeoData Wizard',
  classConfig: {
    method: 'elastic',
    endpoint: elasticsearchEndpoint,
  },
  columnRefinements: [
    van_bag_id_naar_kkg_iri,
    van_wkt_punt_naar_kkg_gebouw,
    van_wkt_punt_naar_kkg_perceel,
    van_woonplaats_naar_kkg_woonplaats,
    van_postcode_en_huisnummer_naar_kkg_nummeraanduiding,
    van_adres_en_woonplaats_naar_kkg_openbareRuimte,
    van_bag_pand_id_naar_kkg_geometrie,
    van_bag_pand_id_naar_kkg_geometrie_bulk,
    van_bag_vbo_id_naar_adres_bulk
  ],
  dataplatformLink: 'https://data.pldn.nl',
  defaultBaseIri: 'https://data.pldn.nl/',
  exampleCSV,
  favIcon: logo,
  getAllowedPrefixes: async () => {
    const response = await fetch('https://api.labs.kadaster.nl/datasets/GeoData-Wizard/vocabulaires/prefixes')
    if (response.ok) {
      const prefixes: PrefixEntry[] = await response.json()
      return prefixes
    } else {
      throw new Error(`Failed to fetch prefixes, status code: ${response.status}`)
    }
  },
  homepageMarkdown,
  icon: logo,
  newDatasetAccessLevel: 'public',
  predicateConfig: {
    method: 'elastic',
    endpoint: elasticsearchEndpoint,
  },
  primaryColor: '#00387d',
  publishOrder: ['download', 'triplyDB'],
  secondaryColor: '#008296',
  triplyDbInstances: [
    {
      label: 'PLDN',
      link: 'https://data.pldn.nl',
    },
    {
      label: 'Kadaster',
      link: 'https://data.labs.kadaster.nl',
    },
  ]
}

export default globalThis.wizardConfig = wizardConfig