import { ColumnRefinement } from "@pldn/ldwizard/types/WizardConfig";
import bulkSparql from "@pldn/ldwizard/bulkSparql";

interface LocationServerResponse {
  response: LocationServerResults;
}

interface LocationServerResults {
  docs: LocationServerResult[];
  numFound: number;
  start: number;
}

interface LocationServerResult {
  adresseerbaarobject_id: string;
  bron: string;
  centroide_ll: string;
  centroide_rd: string;
  gemeentecode: string;
  gemeentenaam: string;
  huis_nlt: string;
  huisnummer: number;
  id: string;
  identificatie: string;
  nummeraanduiding_id: string;
  openbareruimte_id: string;
  openbareruimtetype: string;
  postcode: string;
  provincieafkorting: string;
  provinciecode: string;
  provincienaam: string;
  rdf_seealso: string;
  score: number;
  straatnaam_verkort: string;
  straatnaam: string;
  type: string;
  weergavenaam: string;
  woonplaatscode: string;
  woonplaatsnaam: string;
}

export const van_bag_id_naar_kkg_iri: ColumnRefinement = {
  type: "single",
  label: "Van BAG code naar Kadaster Knowledge Graph IRI",
  description:
    "Gebruikt een kolom met codes die in de Basisregistratie Adressen en Gebouwen (BAG) voorkomen, en vindt de corresponderende IRI in de Kadaster Knowledge Graph. De volgende BAG codes worden ondersteund: nummeraanduiding code, openbare ruimte code, pand code, verblijfsobject code, woonplaats code. Voor meer informatie over de Kadaster Knowledge Graph, zie https://data.kkg.kadaster.nl",
  transformation: async (id: string) => {
    const url = `https://api.labs.kadaster.nl/queries/GeoData-Wizard/van-bag-id-naar-kkg-iri/run?id=${id}`;
    try {
      const results = await fetchCall<{ iri: string }[]>(url);
      if (results.length === 0) {
        console.warn("No results found for refinement query.");
        return undefined;
      }
      return results?.[0]?.iri;
    } catch (err) {
      console.error(err);
      return undefined;
    }
  },
  yieldsIri: true,
  keepOriginalValue: {
    keepValue: true
  }
};

