const pdok_locatie_server =
  "https://api.pdok.nl/bzk/locatieserver/search/v3_1/free?fq=bron:BAG&q=";

export const van_adres_en_woonplaats_naar_kkg_openbareRuimte: ColumnRefinement =
{
  type: "double-column",
  label:
    "Van straatnaam + woonplaats naam naar Kadaster Knowledge Graph openbare ruimte IRI",
  description:
    "Gebruikt deze kolom met straatnamen en een tweede kolom met woonplaats namen, en vindt de corresponderende IRI in de Kadaster Knowledge Graph. Voor meer informatie over de Kadaster Knowledge Graph, zie https://data.kkg.kadaster.nl",
  transformation: async (straat: string, woonplaats: string) => {
    const response = await fetchCall<LocationServerResponse>(
      `${pdok_locatie_server}${straat} ${woonplaats}`
    );
    const id = response.response.docs[0]?.openbareruimte_id;
    if (id === undefined) {
      console.warn("No results found for refinement query.");
      return undefined;
    } else {
      return `https://data.kkg.kadaster.nl/id/openbareRuimte/${id}`;
    }
  },
  yieldsIri: true,
  keepOriginalValue: {
    keepValue: true
  }
};

