async function fetchCall<T>(request: string): Promise<T> {
  console.log("API request to: ", request)
  const response = await fetch(request, {
    method: 'GET',
  })
  if (!response.ok) {
    throw new Error(`${request} returned ${response.status}`);
  } else {
    const jsonResponse = await response.json()
    console.log("fetchCall Json Response: ", jsonResponse)
    return jsonResponse as Promise<T>;
  }
}

// Dit is een voorbeeld van de nieuwe SPARQL bulk processing feature:
export const van_bag_pand_id_naar_kkg_geometrie_bulk: ColumnRefinement = {
  label: "Van BAG pand code naar geometrie (bulk verwerking)",
  type: "single",
  description: "Gebruikt een kolom met pand codes die in de Basisregistratie Adressen en Gebouwen (BAG) voorkomen, en vindt de corresponderende geometrie in de Kadaster Knowledge Graph. Voor meer informatie over de Kadaster Knowledge Graph, zie https://data.kkg.kadaster.nl",
  batchSize: 100,
  bulkTransformation: async (bagIds: string[]) => {
    // make sure to not use blanknodes in the SPARQL query!
  const queryString = `
prefix bag: <http://bag.basisregistraties.overheid.nl/def/bag#>
prefix geo: <http://www.opengis.net/ont/geosparql#>
prefix nen3610: <https://data.kkg.kadaster.nl/nen3610/model/def/>
prefix rdfs: <http://www.w3.org/2000/01/rdf-schema#>
prefix sor: <https://data.kkg.kadaster.nl/sor/model/def/>
select ?wkt {
  ?gebouw a sor:Gebouw;
     geo:hasGeometry ?geometry ;
     sor:geregistreerdMet/nen3610:identificatie ?id.
  ?geometry rdfs:isDefinedBy bag: ; geo:asWKT ?wkt ;
}`
    const sparqlEndpoint = 'https://api.labs.kadaster.nl/datasets/dst/kkg/services/default/sparql'
    const options = { sparqlEndpoint, searchValueName: 'id', transformedValueName: 'wkt' }
    return bulkSparql(queryString, bagIds, options)
  },
  yieldsLiteral: true,
  keepOriginalValue: {
    keepValue: true, 
    customPredicateIRI: "http://www.opengis.net/ont/geosparql#asWKT"
  }
}

// Voor transformatie van BAG Verblijfsobject 
export const van_bag_vbo_id_naar_adres_bulk: ColumnRefinement = {
  label: "Van BAG pand code naar adres gevens (bulk verwerking)",
  type: "single",
  description: "Gebruikt een kolom met pand codes die in de Basisregistratie Adressen en Gebouwen (BAG) voorkomen, en vindt de corresponderende adres in the KKG. Voor meer informatie over de Kadaster Knowledge Graph, zie https://data.kkg.kadaster.nl",
  batchSize: 100,
  bulkTransformation: async (bagIds: string[]) => {
    // make sure to not use blanknodes in the SPARQL query!
  const queryString = `
  prefix bag: <http://bag.basisregistraties.overheid.nl/def/bag#>
  prefix geo: <http://www.opengis.net/ont/geosparql#>
  prefix nen3610: <https://data.kkg.kadaster.nl/nen3610/model/def/>
  prefix rdfs: <http://www.w3.org/2000/01/rdf-schema#>
  prefix sor: <https://data.kkg.kadaster.nl/sor/model/def/>
  prefix skos: <http://www.w3.org/2004/02/skos/core#>
  select ?verblijfsobject ?adres {
    ?verblijfsobject 
      a sor:Verblijfsobject;
      sor:geregistreerdMet/nen3610:identificatie ?id ;
      sor:hoofdadres ?nummeraanduiding . 
    ?nummeraanduiding
      sor:postcode ?postcode ;
      sor:huisnummer ?huisnummer ;
      sor:ligtAan/skos:prefLabel ?straatnaam .
    optional { ?nummeraanduiding sor:huisletter ?huisletter }
    optional { ?nummeraanduiding sor:huisnummertoevoeging ?huisnummertoevoeging }
    
    bind(concat(str(?straatnaam), ' ', str(?huisnummer), 
                if(bound(?huisletter), concat(str(?huisletter)), ''),
                if(bound(?huisnummertoevoeging), concat('-', str(?huisnummertoevoeging)), ''), 
                ', ', str(?postcode)) as ?adres )
  }`
    const sparqlEndpoint = 'https://api.labs.kadaster.nl/datasets/dst/kkg/services/default/sparql'
    const options = { sparqlEndpoint, searchValueName: 'id', transformedValueName: 'adres' }
    return bulkSparql(queryString, bagIds, options)
  },
  yieldsLiteral: true,
  keepOriginalValue: {
    keepValue: true, 
    customPredicateIRI: "http://www.opengis.net/ont/geosparql#asWKT"
  }
}
